.burgerContainer{
    height: 8vh;
    width: 8vh;
    background-color: transparent;
    position: fixed;
    top: 0;
    right: -1vh;
    z-index: 3;
}

@media (orientation: landscape){

    /* nav */ 

    .burgerContainer{
        height: 15vh;
        width: 15vh;
        top: 0;
        right: -1vh;
    }
}


@media (orientation: landscape) and (min-width: 1024px){

    /* nav */ 

    .burgerContainer{
        height: 10vh;
        width: 10vh;
        top: 0;
        right: -1vh;
    }
}

.burgerContainer p{
    width: 50%;
    height: 10%;
    background-color: black;
    position: absolute;
    top: 30%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}

.burgerContainer .midBar{
    top: 50%;
}
.burgerContainer .botBar{
    top: 70%;
}

/* klasy zamykające i otwierające */ 

.burgerContainer .topBar.open{
    -webkit-animation: topBar .3s  both;
            animation: topBar .3s  both;
}
.burgerContainer .midBar.open{
    -webkit-animation: midBar .3s  both;
            animation: midBar .3s  both;
}
.burgerContainer .botBar.open{
    -webkit-animation: botBar .3s  both;
            animation: botBar .3s  both;
}
.burgerContainer .topBar.back{
    -webkit-animation: topBarBack .3s  both;
            animation: topBarBack .3s  both;
}
.burgerContainer .midBar.back{
    -webkit-animation: midBarBack .3s  both;
            animation: midBarBack .3s  both;
}
.burgerContainer .botBar.back{
    -webkit-animation: botBarBack .3s  both;
            animation: botBarBack .3s  both;
}


/* animacje otwierające */


@-webkit-keyframes topBar{
    30%{
        -webkit-transform: translate(-50%,-50%) rotate(-25deg);
                transform: translate(-50%,-50%) rotate(-25deg);
    }
    50%{
        top: 30%;
    }
    100%{
        -webkit-transform: translate(-50%,-50%) rotate(45deg);
                transform: translate(-50%,-50%) rotate(45deg);
        top: 50%;
    }
}


@keyframes topBar{
    30%{
        -webkit-transform: translate(-50%,-50%) rotate(-25deg);
                transform: translate(-50%,-50%) rotate(-25deg);
    }
    50%{
        top: 30%;
    }
    100%{
        -webkit-transform: translate(-50%,-50%) rotate(45deg);
                transform: translate(-50%,-50%) rotate(45deg);
        top: 50%;
    }
}
@-webkit-keyframes midBar{
    30%{
        opacity:1;
    }
    50%{
        opacity:0;
    }
    100%{
        opacity:0;
    }
}
@keyframes midBar{
    30%{
        opacity:1;
    }
    50%{
        opacity:0;
    }
    100%{
        opacity:0;
    }
}
@-webkit-keyframes botBar{
    30%{
        -webkit-transform: translate(-50%,-50%) rotate(25deg);
                transform: translate(-50%,-50%) rotate(25deg);
    }
    50%{
        top: 70%;
    }
    100%{
        -webkit-transform: translate(-50%,-50%) rotate(-45deg);
                transform: translate(-50%,-50%) rotate(-45deg);
        top: 50%;
    }
   
}
@keyframes botBar{
    30%{
        -webkit-transform: translate(-50%,-50%) rotate(25deg);
                transform: translate(-50%,-50%) rotate(25deg);
    }
    50%{
        top: 70%;
    }
    100%{
        -webkit-transform: translate(-50%,-50%) rotate(-45deg);
                transform: translate(-50%,-50%) rotate(-45deg);
        top: 50%;
    }
   
}

/* animacje zamykające */ 

@-webkit-keyframes topBarBack{
    0%{
        top: 50%;
        -webkit-transform: translate(-50%,-50%) rotate(45deg);
                transform: translate(-50%,-50%) rotate(45deg);
    }
    50%{
        top: 30%;
    }
    70%{
        -webkit-transform: translate(-50%,-50%) rotate(-25deg);
                transform: translate(-50%,-50%) rotate(-25deg);
    }
    100%{
        top: 30%;
        -webkit-transform: translate(-50%,-50%);
                transform: translate(-50%,-50%);
    }
   
} 

@keyframes topBarBack{
    0%{
        top: 50%;
        -webkit-transform: translate(-50%,-50%) rotate(45deg);
                transform: translate(-50%,-50%) rotate(45deg);
    }
    50%{
        top: 30%;
    }
    70%{
        -webkit-transform: translate(-50%,-50%) rotate(-25deg);
                transform: translate(-50%,-50%) rotate(-25deg);
    }
    100%{
        top: 30%;
        -webkit-transform: translate(-50%,-50%);
                transform: translate(-50%,-50%);
    }
   
}
@-webkit-keyframes midBarBack{
    0%{
        opacity:0;
        -webkit-transform: translate(-50%,-50%) scaleX(0);
                transform: translate(-50%,-50%) scaleX(0);
    }
    30%{
        opacity:0;
        -webkit-transform: translate(-50%,-50%) scaleX(0);
                transform: translate(-50%,-50%) scaleX(0);
    }
    90%{
        opacity:1;
        
    }
